import { CurrencyPipe } from '@angular/common';
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { ModuleConfigProvider, TranslateProvider } from '@nequi/features/commons/providers';
import {
  ContractNotAcceptedException,
  OneDocThreeInteractor
} from '@nequi/features/one-doc-three/core';
import { FirstTimeMessageBottomSheetService, LoadingService } from '@nequi/ui';

import { HomeConfig } from '../pages/home/home.config';
import { OneDocThreeUserViewModel } from '../view-models/one-doc-three-user.view-model';

@Injectable()
export class OneDocThreeMiddleware implements CanActivate {
  constructor(
    private currencyPipe: CurrencyPipe,
    private oneDocThreeInteractor: OneDocThreeInteractor,
    private oneDocThreeUserViewModel: OneDocThreeUserViewModel,
    private loadingService: LoadingService,
    private applicationDataInteractor: ApplicationDataInteractor,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private translateProvider: TranslateProvider,
    private moduleConfigProvider: ModuleConfigProvider,
    private analyticsInteractor: AnalyticsInteractor
  ) {}

  public async canActivate(
    _route: ActivatedRouteSnapshot,
    _state: RouterStateSnapshot
  ): Promise<boolean | UrlTree> {
    await this.translateProvider.loadModule(this.moduleConfigProvider.i18n.moduleKey);

    this.setAnalyticsPurchaseIntention();

    if (await this.checkBottomSheetVisualization()) {
      const isUserValidated = await this.validateUser();

      if (!isUserValidated) {
        return false;
      }

      this.oneDocThreeUserViewModel.isUserValidated = true;

      const result = await this.showBottomSheet();

      if (result) {
        return true;
      }
    }

    this.oneDocThreeUserViewModel.isUserValidated = false;

    return true;
  }

  public async validateUser() {
    try {
      await this.loadingService.show();

      const validateUserEntity = await this.oneDocThreeInteractor.validateUser();

      this.oneDocThreeUserViewModel.fromValidateUserEntity(validateUserEntity);
      await this.loadingService.hide(true);

      return true;
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();

      return this.processError(error);
    }
  }

  private async processError(error) {
    if (error instanceof ContractNotAcceptedException) {
      this.oneDocThreeUserViewModel.fromValidateUserEntity(error.data);
      this.oneDocThreeUserViewModel.shouldShowTerms = true;

      return true;
    }

    throw error;
  }

  private async checkBottomSheetVisualization() {
    const shouldNotShowFirstMessage = await this.applicationDataInteractor.findValue<boolean>(
      this.moduleConfigProvider.applicationData.oneDocThreeFirstMessage,
      false
    );

    if (!shouldNotShowFirstMessage) {
      return true;
    }

    return false;
  }

  private async showBottomSheet() {
    const bottomSheet = this.translateProvider.get(HomeConfig.i18n.bottomSheetHome);

    const result = await this.firstTimeMessageBottomSheetService.show({
      icon: bottomSheet.image,
      message: bottomSheet.title,
      content: bottomSheet.content,
      cssClass: HomeConfig.bottomSheet.cssClass,
      button: {
        label: bottomSheet.button,
        fill: HomeConfig.bottomSheet.button.fill,
        color: HomeConfig.bottomSheet.button.color,
        action: async () => {
          await this.applicationDataInteractor.save(
            this.moduleConfigProvider.applicationData.oneDocThreeFirstMessage,
            true
          );

          this.firstTimeMessageBottomSheetService.hide(true);
        },
      },
      header: {
        view: true,
        action: () => this.firstTimeMessageBottomSheetService.hide(false),
      },
    });

    return result.data as boolean;
  }

  private setAnalyticsPurchaseIntention() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: HomeConfig.analytics.events.purchaseIntention.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: HomeConfig.analytics.events.purchaseIntention.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: HomeConfig.analytics.events.purchaseIntention.name,
      },
    });
  }
}
