import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { ModuleConfigProvider } from '@nequi/features/commons';
import { FeaturesMoneyManagementModule } from '@nequi/features/money-management/presentation';
import { OneDocThreeInteractor, OneDocThreeRepository } from '@nequi/features/one-doc-three/core';
import { ConfirmInfoBottomSheetService, NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { OneDocThreeConfig } from '../config/one-doc-three.config';
import { OneDocThreeMiddleware } from './middlewares/one-doc-three.middleware';
import { OneDocThreeRoutingModule } from './one-doc-three-routing-module';
import { ConfirmationPage } from './pages/confirmation/confirmation.page';
import { HomePage } from './pages/home/home.page';
import { TermsPage } from './pages/terms/terms.page';
import { OneDocThreeUserViewModel } from './view-models/one-doc-three-user.view-model';

@NgModule({
  declarations: [HomePage, TermsPage, ConfirmationPage],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    OneDocThreeRoutingModule,
    NequiUiModule,
    ReactiveFormsModule,
    FeaturesMoneyManagementModule,
  ],
  providers: [
    OneDocThreeMiddleware,
    ConfirmInfoBottomSheetService,
    OneDocThreeUserViewModel,
    {
      provide: ModuleConfigProvider,
      useValue: OneDocThreeConfig,
    },
    {
      provide: OneDocThreeInteractor,
      deps: [OneDocThreeRepository],
      useFactory: (repository: OneDocThreeRepository) => new OneDocThreeInteractor(repository),
    },
  ],
})
export class OneDocThreeModule {}
