import { FormControl, Validators } from '@angular/forms';

export class TermsViewModel {
  public terms: string;
  private acceptTerms: FormControl;

  public initControl() {
    this.acceptTerms = new FormControl(false, [Validators.required, Validators.requiredTrue]);
  }

  public get termsControl() {
    return this.acceptTerms;
  }
}
