import { Injectable } from '@angular/core';
import { ValidateUserEntity } from '@nequi/features/one-doc-three/core';

Injectable();
export class OneDocThreeUserViewModel {
  public commerceCode: string;
  public contractId: string;
  public lastContractVersion: string;
  public price: number;
  public productName: string;
  public contract: string;
  public url: string;
  public userId: string;
  public email: string;
  public shouldShowTerms: boolean;
  public isUserValidated: boolean;

  public fromValidateUserEntity(entity: ValidateUserEntity) {
    this.commerceCode = entity.commerceCode;
    this.contractId = entity.contractId;
    this.lastContractVersion = entity.lastContractVersion;
    this.price = entity.price;
    this.productName = entity.productName;
    this.contract = entity.contract;
    this.url = entity.url;
    this.userId = entity.userId;
    this.email = entity.email;
  }

  public toValidateUserEntity(): ValidateUserEntity {
    return {
      commerceCode: this.commerceCode,
      contractId: this.contractId,
      price: this.price,
      productName: this.productName,
      contract: this.contract,
      userId: this.userId,
      email: this.email,
    };
  }
}
