import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import {
  ContractNotAcceptedException,
  OneDocThreeInteractor,
  PendingChatException,
  UnavailableServiceException
} from '@nequi/features/one-doc-three/core';
import { NequiWebView } from '@nequi/native/nequi-web-view';
import { CardModalService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { OneDocThreeUserViewModel } from '../../view-models/one-doc-three-user.view-model';
import { HomeConfig } from './home.config';
import { HomeViewModel } from './home.view-model';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage extends FeatureBasePage implements OnInit {
  public viewModel = new HomeViewModel();

  constructor(
    injector: Injector,
    public oneDocThreeUserViewModel: OneDocThreeUserViewModel,
    private oneDocThreeInteractor: OneDocThreeInteractor,
    private translateProvider: TranslateProvider,
    private alertService: AlertService,
    private cardModalService: CardModalService,
    private nequiWebView: NequiWebView,
    private currencyPipe: CurrencyPipe,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);
  }

  public async ngOnInit() {
    const texts = this.translateProvider.get(HomeConfig.i18n.content);

    this.viewModel.items = texts.content;
    this.viewModel.reloadMessage = texts['reload-message'];

    if (this.oneDocThreeUserViewModel.isUserValidated) {
      this.loadPriceToText();
      this.viewModel.setSuccess();

      return;
    }

    await this.validateUser();
  }

  public async validateUser() {
    try {
      this.viewModel.setLoading();

      const validateUserEntity = await this.oneDocThreeInteractor.validateUser();

      this.oneDocThreeUserViewModel.fromValidateUserEntity(validateUserEntity);
      this.loadPriceToText();
      this.viewModel.setSuccess();
    } catch (error) {
      await this.processError(error);
    }
  }

  public validateNavigation() {
    this.setAnalytics();

    if (this.oneDocThreeUserViewModel.shouldShowTerms) {
      this.navController.navigateForward(HomeConfig.routes.oneDocThreeTerms);

      return;
    }

    this.navController.navigateForward(HomeConfig.routes.confirm);
  }

  public openTerms() {
    openUrl(this.oneDocThreeUserViewModel.url);
  }

  private async processError(error) {
    if (error instanceof ContractNotAcceptedException) {
      this.oneDocThreeUserViewModel.fromValidateUserEntity(error.data);
      this.oneDocThreeUserViewModel.shouldShowTerms = true;

      this.loadPriceToText();
      this.viewModel.setSuccess();

      return;
    }

    if (error instanceof PendingChatException) {
      this.oneDocThreeUserViewModel.price = error.data.price;
      this.loadPriceToText();

      this.viewModel.pendingChatParams = {
        url: error.data.url,
        headers: [
          {
            name: HomeConfig.chatHeaders.authorizationKey,
            value: error.data.headers.authorization,
          },
          {
            name: HomeConfig.chatHeaders.userReferenceKey,
            value: error.data.headers.userReference,
          },
        ],
      };

      this.viewModel.setSuccess();

      return this.showPendingChatCardModal();
    }

    if (error instanceof UnavailableServiceException) {
      this.viewModel.setFail(this.viewModel.reloadMessage);

      return this.showErrorAlert();
    }

    this.viewModel.setFail(this.viewModel.reloadMessage);
  }

  private loadPriceToText() {
    this.viewModel.items = this.viewModel.items.map((item) => {
      return {
        ...item,
        title: createFormat(item.title, [
          this.currencyPipe.transform(this.oneDocThreeUserViewModel.price.toString()),
        ]),
      };
    });
  }

  private async showPendingChatCardModal() {
    const modal = this.translateProvider.get(HomeConfig.i18n.modals.pendingChat);

    this.cardModalService.show({
      imgPath: modal.image,
      title: modal.title,
      content: modal.description,
      buttons: [
        {
          text: modal.button,
          handler: () => this.openWebView(),
        },
      ],
    });
  }

  private async showErrorAlert() {
    const alert = this.translateProvider.get(HomeConfig.i18n.alerts.unavailableService);

    this.alertService.show({
      messages: {
        header: alert.title,
        message: alert.message,
        buttons: {
          accept: alert.button,
        },
      },
      handlers: {
        accept: () => this.navController.navigateBack(HomeConfig.routes.closet),
      },
    });
  }

  private async openWebView() {
    await this.nequiWebView.launchWebView({
      url: this.viewModel.pendingChatParams.url,
      title: HomeConfig.pendingChatConfig.webViewTitle,
      loadLabel: HomeConfig.pendingChatConfig.webViewLabel,
      headers: this.viewModel.pendingChatParams.headers,
    });
  }

  private setAnalytics() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: HomeConfig.analytics.events.paymentIntention.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: HomeConfig.analytics.events.paymentIntention.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: HomeConfig.analytics.events.paymentIntention.name,
        eventProps: stringsArrayToObjectWithValues(
          HomeConfig.analytics.events.paymentIntention.clevertap.params,
          [getFormattedDateTimes()]
        ),
      },
    });
  }
}
