import { FormControl, FormGroup } from '@angular/forms';
import { ConfirmInfoPocketItem } from '@nequi/ui';

export class ConfirmViewModel {
  public form: FormGroup;
  public pocketControl: FormControl = new FormControl();

  public buildForm() {
    this.form = new FormGroup({
      pocket: this.pocketControl,
    });

    this.form.markAsPristine();
    this.form.markAsUntouched();
  }

  public buildArrayConfirmationItems(
    messages: any,
    amount: string,
    email: string
  ): ConfirmInfoPocketItem[] {
    return [
      {
        label: messages.items.product.label,
        value: messages.items.product.name,
      },
      {
        label: messages.items.email,
        value: email,
      },
      {
        label: messages.items.amount,
        value: amount,
      },
    ];
  }
}
