import { BaseMapper } from '@nequi/features/commons';

import { ConnectionEntity } from '../../core/entities/connection.entity';
import { ConfirmationConfig } from '../pages/confirmation/confirmation.config';

export abstract class ConnectionMaper extends BaseMapper<ConnectionEntity> {
  public static from(data: any): ConnectionEntity {
    return {
      url: data.connection.url,
      headers: [
        {
          name: ConfirmationConfig.headersForChat.authorizationKey,
          value: data.connection.headers.authorization,
        },
        {
          name: ConfirmationConfig.headersForChat.userReferenceKey,
          value: data.connection.headers.userReference,
        },
      ],
    };
  }
}
