interface PendingChatParams {
  url: string;
  headers: Array<{
    name: string;
    value: string;
  }>;
}

export class HomeViewModel {
  public items: Array<{ icon: string; title: string; description?: string }> = [];
  public isLoading = false;
  public isSuccess = false;
  public isFailed = false;
  public failedMessage = null;
  public pendingChatParams: PendingChatParams;
  public reloadMessage: string;

  public setLoading() {
    this.isLoading = true;
    this.isSuccess = false;
    this.isFailed = false;
    this.failedMessage = null;
  }

  public setSuccess() {
    this.isLoading = false;
    this.isSuccess = true;
    this.isFailed = false;
    this.failedMessage = null;
  }

  public setFail(failedMessage: string) {
    this.isFailed = true;
    this.failedMessage = failedMessage;
    this.isLoading = false;
    this.isSuccess = false;
  }
}
