import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { AnalyticsInteractor, AnalyticsType, LoggerInteractor } from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  ConnectionEntity,
  DOCPaymentTransaction,
  ValidateUserEntity
} from '@nequi/features/one-doc-three/core';
import { NequiWebView } from '@nequi/native/nequi-web-view';
import { CardModalService, ConfirmInfoPocketProps } from '@nequi/ui';

import { ConnectionMaper } from '../../mappers/connectionMapper';
import { OneDocThreeUserViewModel } from '../../view-models/one-doc-three-user.view-model';
import { ConfirmViewModel } from './confirm.view-model';
import { ConfirmationConfig } from './confirmation.config';

@Component({
  selector: 'app-confirmation',
  templateUrl: './confirmation.page.html',
  styleUrls: ['./confirmation.page.scss'],
})
export class ConfirmationPage extends TransactionBasePage implements OnInit {
  public viewModel: ConfirmViewModel = new ConfirmViewModel();

  constructor(
    public oneDocThreeUserViewModel: OneDocThreeUserViewModel,
    private currencyPipe: CurrencyPipe,
    private nequiWebView: NequiWebView,
    private cardModalService: CardModalService,
    private loggerInteractor: LoggerInteractor,
    private analyticsInteractor: AnalyticsInteractor,
    injector: Injector
  ) {
    super(injector);
  }

  public async ngOnInit() {
    await this.loadingService.show();
    this.viewModel.buildForm();
    await this.loadingService.hideWithoutFeedback();
  }

  public async navigate() {
    this.pay();
  }

  protected onTransactionSuccess(response: unknown) {
    this.setAnalytics();
    this.successResponse(ConnectionMaper.from(response));
  }

  protected onTransactionFailed(error: CoreException) {
    if (error.constructor !== CoreException) {
      throw error;
    }

    this.toastService.showError(error.message);
  }

  protected async getConfirmationInfo(): Promise<ConfirmInfoPocketProps> {
    const messages = this.translateProvider.get(ConfirmationConfig.i18n.confirmBottomSheet);

    return {
      title: messages.title,
      subtitle: messages.subtitle,
      items: this.viewModel.buildArrayConfirmationItems(
        messages,
        this.currencyPipe.transform(this.oneDocThreeUserViewModel.price),
        this.oneDocThreeUserViewModel.email
      ),
      confirmLabel: messages.title,
      viewCancel: false,
    };
  }

  private async pay() {
    const data: ValidateUserEntity = this.oneDocThreeUserViewModel.toValidateUserEntity();
    const pocket = this.viewModel.pocketControl.value;
    const transaction = new DOCPaymentTransaction()
      .withData(data)
      .withPocket(pocket)
      .withAmount(this.oneDocThreeUserViewModel.price);

    super.executeTransaction<unknown>(transaction);
  }

  private async successResponse(response: ConnectionEntity) {
    const messages = this.translateProvider.get(ConfirmationConfig.i18n.cardModal);

    this.cardModalService.show({
      imgPath: messages.icon,
      title: messages.title,
      content: messages.content,
      buttons: [
        {
          text: messages.buttons.next,
          handler: () => this.openWebView(response),
        },
      ],
    });
  }

  private async openWebView(response: ConnectionEntity) {
    try {
      await this.nequiWebView.launchWebView({
        url: response.url,
        title: ConfirmationConfig.webViewTitle,
        loadLabel: ConfirmationConfig.webViewLabel,
        headers: response.headers,
      });
    } catch (err) {
      this.loggerInteractor.error(`[OneDocThree][webview] ${JSON.stringify(err)}`);
    }

    this.navigatorProvider.navigateForward(ConfirmationConfig.routes.closet);
  }

  private setAnalytics() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: ConfirmationConfig.analytics.events.successfulPayment },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: ConfirmationConfig.analytics.events.successfulPayment },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: { eventName: ConfirmationConfig.analytics.events.successfulPayment },
    });
  }
}
