import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { OneDocThreeMiddleware } from './middlewares/one-doc-three.middleware';
import { ConfirmationPage } from './pages/confirmation/confirmation.page';
import { HomePage } from './pages/home/home.page';
import { TermsPage } from './pages/terms/terms.page';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
    canActivate: [OneDocThreeMiddleware],
  },
  {
    path: 'terms',
    component: TermsPage,
  },
  {
    path: 'confirm',
    component: ConfirmationPage,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class OneDocThreeRoutingModule {}
