import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { OneDocThreeInteractor } from '@nequi/features/one-doc-three/core';
import { LoadingService, ToastService } from '@nequi/ui';

import { OneDocThreeUserViewModel } from '../../view-models/one-doc-three-user.view-model';
import { TermsConfig } from './terms.config';
import { TermsViewModel } from './terms.view-model';

@Component({
  selector: 'app-terms',
  templateUrl: './terms.page.html',
  styleUrls: ['./terms.page.scss'],
})
export class TermsPage implements OnInit {
  public viewModel = new TermsViewModel();

  constructor(
    private oneDocThreeInteractor: OneDocThreeInteractor,
    private oneDocThreeUserViewModel: OneDocThreeUserViewModel,
    private navController: NavController,
    private loadingService: LoadingService,
    private toastService: ToastService
  ) {}

  public ngOnInit() {
    this.viewModel.initControl();
    this.viewModel.terms = this.oneDocThreeUserViewModel.contract;
  }

  public goBack() {
    this.navController.back();
  }

  public async acceptContract() {
    await this.loadingService.show();

    try {
      await this.oneDocThreeInteractor.acceptTerms({
        url: this.oneDocThreeUserViewModel.url,
        productName: this.oneDocThreeUserViewModel.productName,
        contractId: this.oneDocThreeUserViewModel.contractId,
        commerceCode: this.oneDocThreeUserViewModel.commerceCode,
        lastContractVersion: this.oneDocThreeUserViewModel.lastContractVersion,
        contract: this.oneDocThreeUserViewModel.contract,
        price: this.oneDocThreeUserViewModel.price.toString(),
        userId: this.oneDocThreeUserViewModel.userId,
      });

      await this.loadingService.hide(true);

      this.navController.navigateBack(TermsConfig.routes.confirm);
    } catch (error) {
      await this.loadingService.hide(false);

      await this.toastService.showError(error.message);
    }
  }
}
